@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);

* {
  padding: 0;
  margin: 0;
  font-family: 'Quicksand', sans-serif;
}

.bg-default {
  background-color: #F7F7F2;
}

.bg-light-default {
  background-color: #E4E6C3;
}

h1, h2, h3, h4, h5, h6 {
  color: #899878;
}

.text-default {
  color: #899878;
}

nav{
    border-top: 1px solid black;
    border-bottom: 1px solid black
}


a{
    color: Black;
}

a:hover{
    color: rgb(95, 92, 92);
}




.promoImg{
    max-height: 1000px;
    width: 100%;
}
.card-container {
    margin: 2px;
    max-width: 250px;
    height: 100%;
    padding: 5px;
}

.card-container:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
}
.form-group{
    padding-top: 3px;
    padding-bottom: 3px;
}
.footer {
    background-color: #E4E6C3;
    justify-content: center;
}
